/* Zajednicke postavke  */

.izletintro,
.highlights,
.bookinfo,
.includedinprice,
.izletdetail {
  max-width: var(--maxSirina);
  margin: 5rem auto;
  padding-top: 10px;
  padding-bottom: 20px;
  @media (--tablet) {
    padding-left: var(--marginSadrzaj);
    padding-right: var(--marginSadrzaj);
  }
}

/* Izlet intro tekst  */

.izletintro {
  & h2 {
    /* // font-size: 2em;
    // font-size: calc(0.75em + 3vmin); */
    text-align: center;
  }
}

.highlights {
  background-color: #fff;
  padding: var(--marginSadrzaj);
  & h2 {
    text-align: center;
    /* color: var(--plava); */
  }
  & li {
    margin-bottom: 0.8em;
  }
  & ul {
    list-style-position: outside;
    margin: 0 auto;
    padding: 0;
    @media (--mobile) {
      column-count: 2;
      column-fill: balance;
      column-gap: 40px;
      column-rule-style: dotted;
      column-rule-width: 1px;
      column-rule-color: var(--svsiva);
    }

    @media (--tablet) {
      column-count: 3;
    }

    & li {
      /* margin-left: 20px; */
      break-inside: avoid;
      padding: 0;
      margin: 0 0 1.5rem 2rem;
    }
  }
  & li {
  }
}

/* Izlet booking details  */

.bookinfo {
  padding: 0;
  border: 2px solid var(--svplava);
  background-color: #fff;
  /* background: linear-gradient(
    139deg,
    rgba(255, 255, 255, 1) 20%,
    rgba(150, 220, 250, 1) 80%
  ); */
  /* background: repeating-linear-gradient(
    0deg,
    rgb(var(--c-bgplava) / 0.4),
    rgb(var(--c-bgplava) / 0.4) 8px,
    rgb(var(--c-bgplava) / 0.2) 8px,
    rgb(var(--c-bgplava) / 0.2) 16px
  ); */
  background: repeating-linear-gradient(
    45deg,
    rgb(var(--c-zuta) / 1),
    rgb(var(--c-zuta) / 1) 50px,
    rgb(var(--c-zuta) / 0.8) 50px,
    rgb(var(--c-zuta) / 0.8) 100px
  );
}

.charter .bookinfo {
  background: none;
  background-color: #fff;
  border: 0;
  border-top: 1px;
}
.mainbookinfo {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
  /* align-items: start; */
}

.charter .mainbookinfo {
  background-color: transparent;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));

  grid-template-rows: auto;
  & > * {
    padding: 20px;
    margin: 0;
    border: 1px solid var(--bgsiva);
    border-top: 0;
    border-left: 0;
  }
  & h4 {
    text-transform: uppercase;
    /* border-bottom: 1px dotted var(--bgplava); */
    padding-bottom: 0.5rem;
    font-size: 0.9em;
  }
}

.basicdata {
  grid-row: 1;
  grid-column: 1;
  @media (--mobile) {
    grid-row: 1;
    grid-column: 1 / 3;
  }
  @media (--tablet) {
    grid-row: 1;
    grid-column: 1 / 2;
  }
  background-color: var(--svplava);
  padding: 20px;

  & h4 {
    text-transform: uppercase;
    border-bottom: 1px dotted #fff;
    padding-bottom: 0.5rem;
    font-size: 0.9em;
    margin: 2rem 0 0.75rem 0;
  }
  & p {
    margin: 0 0 0.5rem;
  }
  & > * {
    color: #fff;
  }
}

.bookingdates {
  grid-row: 2;
  grid-column: 1;
  @media (--mobile) {
    grid-row: 2;
    grid-column: 1 / 2;
  }
  @media (--tablet) {
    grid-row: 1;
    grid-column: 2 / 3;
  }
  padding: 20px;
  /* background-color: var(--bgplava); */
  /* background-color: var(--zuta); */
  text-align: center;
  /* display: grid;

  grid-template-columns: 1fr 1fr; */
  & a[name='bookanchor'] {
    /* display: block;
    background-color: #f00;
    width: 50px;
    height: 50px; */
    /* grid-row: 1;
    grid-column: 1; */
  }
  /* & h3 {
    grid-column: 1 / span 2;
  } */
  & .bookinglist {
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
    & li {
      font-size: var(--bodySize);
      flex: 0 1 75px;
      & span {
        font-size: 0.6em;
      }
    }
  }
}

.selectadditionaldate {
  grid-row: 3;
  grid-column: 1;
  @media (--mobile) {
    grid-row: 2;
    grid-column: 2 / 3;
  }
  @media (--tablet) {
    grid-row: 1;
    grid-column: 3 / 4;
  }

  padding: 20px;
  text-align: center;
  /* background-color: var(--bgplava); */

  display: flex;
  flex-direction: column;
  align-items: center;
  /* & label {
    display: none;
  } */
  & #aviabledates {
    text-transform: uppercase;
    padding: 5px 10px;
    margin: 0.5rem auto;
    background-color: rgb(255 255 255 / 0.4);
    /* border-bottom: 3px solid var(--tplava); */
    &:hover {
      background-color: rgb(255 255 255 / 0.8);
    }
    &:before {
      content: url('calendar-tplava.svg');
      display: inline-block;
      width: 24px;
      height: 24px;
      margin-right: 5px;
      top: 5px;
      position: relative;
      /* margin-top: -5px;
      background-image: url('calendar.svg');
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover; */
    }
  }
  & #bookadditionaldate {
    display: none;
    margin: 0.5rem auto;
    padding: 10px 20px;
    background-color: var(--zelena);
    border-bottom: 6px solid var(--tplava);
    color: #fff;
    &:hover {
      background-color: var(--plava);
    }
  }
}

/*  Book now buton - fiksan na dnu stranice */

.bookstatic {
  z-index: 900;
  width: 100%;
  width: 100vw;
  position: fixed;
  bottom: 0;
  display: block;
  text-align: center;
  padding: 10px;
  text-transform: uppercase;
  letter-spacing: 0.02rem;
  border-top: 4px solid var(--tplava);
  background-color: var(--zuta);
  color: var(--tplava);
  cursor: pointer;
  &:hover {
    background-color: var(--plava);
    color: #fff;
  }
}

/* Galerija slika */

.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-wrapper {
  max-width: var(--maxSirina);
}

.swiper-slide,
.gal-item {
  text-align: center;
  font-size: 18px;
  display: block;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Video wrapper */

.izlet-video-wrapper {
  max-width: var(--maxSirina);
  margin: 5rem auto;
  & p {
    font-size: 0.7em;
    margin: 5px 10px;
    padding: 0;
    color: var(--siva);
  }
}

.izlet-video {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.izlet-video iframe,
.izlet-video object,
.izlet-video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* */

.includedinprice {
  padding: 20px;
  & ul {
    padding: 0 20px;
  }
}

.izletdetail {
  padding: 20px;
  & ul {
    padding: 0 20px;
  }
}

/* Odabir izleta SELECT */

.izlet-promolink {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  overflow-x: hidden;
  margin: 2em auto 0;

  & a,
  & p {
    margin: 0 10px 10px 10px;
    @media (--mobile) {
      border-radius: 10px 10px 0 0;
      margin: 0 10px;
    }

    text-transform: uppercase;
    letter-spacing: 0.05em;
    /* border-radius: 2px; */
    /* font-size: 1em; */
    flex: 0 1 300px;
    /* align-self: center; */
    padding: 8px 15px;
    /* border-bottom: 1px dotted white;
    border-bottom: 4px solid rgb(var(--c-plava) / 0.8); */
    color: #fff;
    /* background-color: var(--plava);
    background-color: rgb(var(--c-plava) / 0.4); */
    background-color: var(--tplava);

    /* background-color: rgba($plava, 0.7); */
    transform: all 0.2s linear;
    font-size: 1.4rem;
  }

  & p {
    padding: 14px 20px;
    padding: 0;
    text-align: center;
    &:hover {
      background-color: var(--zelena);
    }
  }

  & a:not(:last-child) {
    padding: 14px 20px;
    text-align: center;
  }

  & a:hover {
    background-color: var(--zelena);
    background-color: var(--zelena);
    color: #fff;
  }
}

.select-izleti {
  display: block;
  font-size: 1.4rem;
  font-weight: 337;
  color: #fff;
  padding: 14px 40px 14px 20px;
  width: 250px;
  max-width: 80%;
  box-sizing: border-box;
  margin: 0 auto;
  border: none;
  appearance: none;
  background-color: transparent;
  background: url(strelica-select.svg) 100% 50% no-repeat;
  background-size: 18px auto, 100%;
  transform: all 0.2s linear;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.select-izleti::-ms-expand {
  display: none;
}

.select-izleti:focus {
  border-color: #fff;
  color: #fff;
  outline: none;
}
.select-izleti option {
  font-weight: normal;
}

/* izbornik na dnu stranice */

.fast-menu-bottom {
  box-shadow: 0px -1px 15px 0px rgba(0, 0, 0, 0.54);
  z-index: 900;
  width: 100%;
  width: 100vw;
  position: fixed;
  bottom: 0;
  /* display:flex; */
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  align-items: center;
  justify-items: center;
  /* border-top: 4px solid var(--tplava); */
  background-color: var(--zuta);
  color: var(--tplava);
  cursor: pointer;
  /* &:hover {
    background-color: var(--plava);
    color: #fff;
  } */
}

.fast-menu-bottom-home {
  background-color: var(--svplava);
  padding: 14px 20px 14px 20px;
  margin: 0;

  text-align: center;
  &:hover {
    background-color: var(--zelena);
    color: #fff;
  }
  font-size: 1.4rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  width: 100%;
  /* display: block; */
  display: flex;
  justify-content: center;
  align-items: center;
  &:before {
    content: '';
    display: block;
    margin-right: 10px;
    width: 20px;
    height: 20px;
    background: url(home.svg) 50% 50% no-repeat;
    background-size: cover;
  }
}

.fast-menu-bottom-booking {
  background-color: var(--zuta);
  padding: 14px 20px 14px 20px;
  margin: 0;

  text-align: center;
  &:hover {
    background-color: var(--zelena);
    color: #fff;
  }
  font-size: 1.4rem;
  color: var(--tplava);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  &:before {
    content: '';
    display: block;
    margin-right: 5px;
    width: 22px;
    height: 20px;
    background: url(check-plavi.svg) 50% 50% no-repeat;
    background-size: cover;
  }
}
