.zaglavlje {
  background-color: var(--tplava);
  background-image: none;
}

.zaglavlje .logo {
  /* font-size: 0.5em; */
  /* @include breakpoint($tablet) {
        font-size: 7px;
    }
    @include breakpoint($large) {
        font-size: 0.6em;
    } */
  /* @include breakpoint($wide) {
        font-size: 0.71em;
	} */
  display: inline-block;
  width: 292px;
  height: 34px;
  margin: 0.7rem 1rem 0.7rem 2.5rem;
  text-indent: -99999px;
  overflow: hidden;
  background: url(croatiaexcursion-logo.svg) 100% 50% no-repeat;
  background-size: cover;
  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 1.3rem;
    left: 1.3rem;
    width: 20px;
    height: 20px;
    background: url(home.svg) 50% 50% no-repeat;
    background-size: cover;
  }
}

.footer {
  padding: 20px;
  font-size: 1.4rem;
  background-color: var(--zuta);
  text-align: center;

  & .printcontacts {
    margin-top: 2em;
    border-top: 1px solid #333;
    display: none;
  }
  & a,
  & a:visited {
    color: var(--tplava);
    font-weight: bold;
    &:hover {
      color: var(--plava);
    }
  }
}

.userbar {
}

.userbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-right: 50px;
  font-size: 1.2rem;
  background-color: var(--zuta);
  color: var(--tplava);
  margin-bottom: 8px;

  /*mjesto za menu buton */

  & .space {
    flex: 10 auto;
  }

  & .menujezik {
    display: none;
    & a {
      padding: 0.2em 0.3em;
      color: var(--tplava);

      &.active {
        color: var(--zelena);
      }

      &:hover,
      &:hover.active {
        color: var(--zelena);
      }
    }
    @media (--tablet) {
      display: block;
      flex: 0 0 auto;
      margin: 0.2em 0.4em;
      & ul {
        padding: 0;
      }
      border-right: 1px dotted var(--tplava);
    }
    @media (--large) {
      margin: 0.2em 1em;
      border-right: none;
    }
  }

  & .menukontakti {
    display: none;

    @media (--tablet) {
      display: flex;
      margin: 0.2em 0.4em;
      color: var(--plava);
      flex: 0 0 auto;

      & a {
        color: var(--tplava);
        margin: 0.2em 2px;
      }

      border-right: 1px dotted var(--tplava);
    }

    @media (--large) {
      margin: 0.2em 1em;
      border-right: none;
    }
  }

  & .userlogin {
    display: none;

    @media (--tablet) {
      display: block;
      flex: 0 0 auto;

      margin: 0.2em 0.8em 0.2em 0.4em;

      color: var(--tplava);
      background-color: #fff;

      &:hover {
        background-color: var(--svsiva);
      }
    }
  }

  & .currentuser {
    display: block;
    background-color: transparent;
    margin: 0.2em auto 0.2em auto;
    padding: 0.2em 0.3em;
    display: block;
    flex: 0 0 auto;
    color: var(--tplava);

    &:hover {
      background-color: transparent;
    }
  }
}

/* // PROMO sekcija */
.promo {
  background-color: #fff;
}

#promo-zaglavlje {
  & .owl-stage-outer {
    width: 100%;
  }

  & .owl-item {
    display: block;
    width: 100%;
    height: auto;
  }
}

/* // promo slike na početnoj stranici */
.promoslide {
  display: block;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 55vh;
  text-align: center;
  background-size: cover;
  /* background-position: 50%; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  /* @include breakpoint($okomiti) {
        height: 50vh;
    }
    @include breakpoint($vodoravni) {
        height: 75vh;
    } */
}

/* // promo slika na stranici izleta */
.headerpromo {
  width: 100%;
  min-height: 50vh;
  /* background-color: var(--plava);
	 */
  background-color: transparent;
}

.headerpromo-video {
  object-fit: cover;
  object-position: 50% 50%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.headerpromoinside {
  margin: 0 auto;
  /* // max-width: 90vw;  // ne radi u IE11 */
  width: 100%;
  align-self: center;
  text-align: center;
  color: #fff;
  padding: 20px;

  /* z-index:2; */
  & h1 {
    /* width: 80%;
    max-width: 90vw; */
    color: #fff;
    margin: 0.2em auto;
    text-shadow: 2px 3px 5px rgb(0 0 0 / 0.2);
  }

  & h2 {
    /* width: 80%;
    max-width: 90vw;
    line-height: 1em; */
    color: var(--zuta);
    margin: 0.1em auto;
    /* font-size: 2em;
    font-size: calc(0.75em + 2.5vmin); */
    text-shadow: 2px 3px 5px rgb(0 0 0 / 0.2);
  }

  & h2.fancy {
    width: 80%;
    max-width: 90vw;
    text-align: center;
    letter-spacing: 0.05rem;
    @media (--large) {
      padding-left: 150px;
    }
  }
}

/* promo link u zaglavlju */

.headerpromo .promolink {
  display: none;

  @media (--tablet) {
    display: flex;
    justify-content: center;
  }
}

.promolink {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  overflow-x: hidden;
  margin: 2em auto;

  & a,
  & p {
    text-transform: uppercase;
    letter-spacing: 0.05em;
    border-radius: 2px;
    /* font-size: 1em; */
    flex: 0 1 auto;
    align-self: center;
    padding: 8px 15px;
    border-bottom: 1px dotted white;
    border-bottom: 4px solid rgb(var(--c-plava) / 0.8);
    color: #fff;
    background-color: var(--plava);
    background-color: rgb(var(--c-plava) / 0.4);
    /* background-color: rgba($plava, 0.7); */
    transform: all 0.2s linear;
  }

  & p {
    padding: 14px 20px;

    margin: 0 10px;
    text-align: center;
  }

  & a:not(:last-child) {
    margin-right: 10px;
    margin-left: 10px;
  }

  & a:hover {
    background-color: var(--zelena);
    background-color: var(--zelena);
    color: #fff;
  }
}

/* // why book with use */
.whyus {
  /* background: #eee; */
  text-align: center;
  margin: 5rem auto;
  padding-left: var(--marginSadrzaj);
  padding-right: var(--marginSadrzaj);
  max-width: var(--maxSirina);

  & h3 {
    /* font-size: 1.7em;
    color: var(--tplava); */
    margin: 0 0 0.5em;
    padding: 0;
  }

  & h3.harmoplus {
    font-size: 1.5em;
    font-size: calc(1em + 0.5vmin);
    color: var(--plava);
    text-transform: uppercase;
    margin: 0;
    padding: 0;
  }

  & h4 {
    /* font-size: 1em; */
    color: var(--siva);
    margin: 0;
    padding: 0 0.5em 0.5em;
  }

  & .nika h4 {
    font-size: 1em;
    font-size: calc(1em + 0.5vmin);
    color: var(--siva);
    margin: 0;
    padding: 0 0.5em 0.5em;
  }

  & ul {
    max-width: 1200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    padding: 0.5em;
    list-style: none;

    & li {
      flex: 1 1 90%;
      list-style-type: none;

      @media (--large) {
        flex: 1 1 15%;
      }
    }

    & li:not(:last-child) {
      padding-bottom: 1em;
      border-bottom: 1px solid #ccc;
      margin-bottom: 0.5em;

      @media (--large) {
        padding: 0;
        border: none;
        margin: 0;
        padding-right: 1em;
        border-right: 1px solid #aaa;
        margin-right: 1em;
      }
    }
  }
}

/* // Akcija */

.akcija {
  /* box-sizing: border-box; */
  margin: -30px auto 15px;
  text-align: center;
  display: block;
  /* background-color: rgb(var(--c-zuta) / 0.8); */

  /* border-top: 16px solid rgb(var(--c-zuta) / 0.5); */
  /* border-left: 16px solid var(--zuta);
  border-right: 16px solid var(--zuta); */

  /* border-left-color: var(--tplava);
  border-right-color: var(--tplava); */

  /* border-radius: 0 0 50% 50%; */

  /* @media (--tablet) {
    margin: -50px auto 20px;

    max-width: 600px;
  } */

  & a {
    display: inline-block;
    margin: 0 auto;
    max-width: 90%;
    background-color: #fff;
    text-decoration: none;
    text-align: center;
    transition: all 0.3s linear;
    padding: 24px 36px;
    border-bottom: 6px solid var(--plava);
    border-radius: 32px 32px 0 0;
    decoration: none;
    &:hover {
      border-color: var(--zuta);
      & h2,
      & h3,
      & h4 {
        color: var(--plava);
      }
    }
  }

  & h4 {
    color: var(--tplava);
    margin: 0 auto;
  }
  & h3 {
    margin: 0 auto;
    text-transform: uppercase;
  }
  & p {
    margin: 5px auto;
  }
}

/* // TRIP ADVISOR REVIEWS I WIDGET */
.triper {
  margin: 5rem auto;
  text-align: center;
  padding-left: var(--marginSadrzaj);
  padding-right: var(--marginSadrzaj);
  max-width: var(--maxSirina);
}

#CDSWIDSSP {
  width: 100% !important;
}

.widSSPData {
  border: none !important;
}

/* Harmonika  */

.harmodole {
  font-size: 0.85em;
  background-color: $ssiva;
  display: inline-block;
  padding: 5px 20px 10px;
  text-align: center;
  transform: all 0.2s ease-in-out;
  margin-bottom: 0;
  & span {
  }
  & span:after {
    content: '';
    @include sprite(st-dole-plava, all);
    font-size: 0.8em;
    margin: 0 auto;
    display: block;
  }
  &:hover {
    cursor: pointer;
    background-color: #fff;
  }
  &.isOpen {
    background-color: #fff;
    &:hover {
      color: var(--siva);
    }
    & span:after {
      transform: rotate(180deg);
    }
  }
}

.nika .harmodole {
  background-color: #fff;
  &:hover {
    background-color: var(--zuta);
  }
}

.harmo {
  position: relative;
  text-align: center;
}

.nika {
  max-height: 0;
  overflow: hidden;
  margin: 0;
  max-width: var(--maxSirina);
  margin: 0 auto;
  padding-top: 0px;
  padding-bottom: 0px;
  @media (--tablet) {
    padding-left: var(--marginSadrzaj);
    padding-right: var(--marginSadrzaj);
  }
}

.isDown {
  max-height: 100%;
  @media (--large) {
    max-height: 15000px;
  }
  margin-top: 1em;
}

.harmoplus {
  display: flex;
  justify-content: center;
  align-items: center;
  &:hover {
    cursor: pointer;
  }
  &:after {
    /* margin: 10px auto 0; */
    /* display: block; */
    content: '';
    margin-left: 10px;
    width: 26px;
    height: 26px;
    background: url(plus.svg) 50% 50% no-repeat;
    animation: 3s pulsiranje linear infinite;
  }
  &.isOpen:after {
    background: url(minus.svg) 50% 50% no-repeat;
    animation: none;
  }
}

@keyframes pulsiranje {
  0% {
    transform: scale(1);
  }
  80% {
    transform: scale(1);
  }
  90% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

h3.harmoplus {
}

h1.harmoplus {
  font-size: 2em;
  font-size: calc(1.5em + 0.5vmin);
  &:after {
    font-size: 0.75em;
  }
}

/* CAROUSEL */

.swiper-container .swiper-button-next,
.swiper-container .swiper-button-prev {
  box-sizing: content-box;
  background-color: #fff;
  background-color: rgb(255 255 255 / 0.8);
  padding: 5px 10px;
  color: var(--tplava) !important;
  transition: background-color 0.2s linear;
  &:hover {
    background-color: #fff;
    color: var(--plava) !important;
  }
}

.swiper-container .swiper-button-next {
  right: 0;
  border-radius: 5px 0 0 5px;
}

.swiper-container .swiper-button-prev {
  left: 0;
  border-radius: 0 5px 5px 0;
}

.swiper-container {
  & .swiper-pagination-progressbar {
    height: 8px !important;
    background-color: var(--zuta);
  }
  & .swiper-pagination-progressbar-fill {
    background: var(--tplava) !important;
  }
}

/* printlink */

.printlink {
  display: flex;
  margin-top: 1em;
  margin-bottom: 1em;
  font-size: 0.8em;
  text-transform: uppercase;
  color: #333;
  background-color: #f0f0f0;
  &:hover {
    color: var(--plava);
    background-color: #fff;
  }
  padding: 0.5em 1.4em;
  border: 1px solid #333;
  border-radius: 2em;
  display: inline-block;
  line-height: 2em;
  &:before {
    content: '';
    display: inline-block;
    background: url(print.svg) 50% 50% no-repeat;
    width: 18px;
    height: 18px;
    margin-right: 5px;
    position: relative;
    top: 0.55em;
  }
}
