.izletbrief {
  /* background-color: rgba(#fff, 0.2); */
  max-width: 30%;
  min-height: 200px;
  text-align: center;
  background-color: var(--izletBriefColor);
  background-color: rgb(var(--c-izletBriefColor) / 0.7);
  background: linear-gradient(
    to bottom,
    rgb(var(--c-izletBriefColor) / 1),
    rgb(var(--c-izletBriefColor) / 0)
  );
  /* flex: 0 1 auto;
  align-self: auto; */
  padding: 20px 15px;
  & p {
    color: #fff;
  }
  & .cijenaod {
    display: block;
    margin: 0;
    line-height: 1.1em;
    font-size: 0.45em;
    font-weight: normal;
    text-transform: uppercase;
    text-align: left;
  }
  & .cijena {
    font-size: 1.6em;
    line-height: 1em;
    padding: 10px 0 0;
    margin: 0;
    font-weight: bold;
    border-top-width: 2px;
    border-top-style: dotted;
    &:after {
      content: ' KN';
      font-size: 0.5em;
      font-weight: normal;
    }
  }
  & .cijenafrom:before {
    content: 'FROM';
    display: block;
    margin: 0;
    line-height: 1.1em;
    font-size: 0.45em;
    font-weight: normal;
    text-align: left;
  }
  & .cijenaodhr:before {
    content: 'OD';
    display: block;
    margin: 0;
    line-height: 1.1em;
    font-size: 0.45em;
    font-weight: normal;
    text-align: left;
  }
  & .cijenaeur {
    font-size: 1.6em;
    line-height: 1em;
    padding: 0 0 10px 0;
    margin: 0;
    font-weight: bold;

    &:after {
      content: ' EUR';
      font-size: 0.5em;
      font-weight: normal;
    }
  }
  & .trajanje {
    position: relative;
    font-size: 2em;
    line-height: 0.8em;
    padding: 0;
    margin: 0;
    font-weight: bold;
    &:before {
      content: '\2248';
      font-size: 0.5em;
      font-weight: normal;
    }
    &:after {
      content: 'h';
      font-size: 0.5em;
      font-weight: normal;
    }
  }
  & .osobe {
    margin-top: 0;
    font-size: 1.6em;
    line-height: 0.8em;
    &:after {
      content: '';

      width: 12px;
      height: 22px;
      background: url(osoba-bijela.svg) 50% 50% no-repeat;
      background-size: cover;
      display: inline-block;
      margin: 0;
      padding: 0;
      line-height: 0;
    }
  }
}

