/* Ikone tipa izleta */
.izletbrief:before {
  content: '';
  display: block;
  /* opacity: 0.5; */
  margin: 0;
  /* font-size: 1.2em;
     */
  width: 100%;
  height: 16px;
  background-size: cover;
  margin-bottom: 10px;
}

.brod .izletbrief:before {
  background: url(boat-bijeli.svg) 50% 50% no-repeat;
}

.bus .izletbrief:before {
  background: url(bus-bijeli.svg) 50% 50% no-repeat;
}

.selfdrive .izletbrief:before {
  background: url(car-bijeli.svg) 50% 50% no-repeat;
}

/* // prikaz bookinjgdatuma na prikazu svih izleta */

.bookinglist {
  /* background-color: #eee; */
  padding: 10px 10px;
  margin: 0;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: center;
  justify-content: center;
  & li {
    font-size: 0.7em;
    text-align: center;
    padding: 0;
    margin: 0;
    list-style-type: none;
    text-align: center;
    background-color: var(--zelena);
    flex: 1 1 20%;
    max-width: 120px; /* da ne budu presiroki kad je samo jedan izlet po retku */
    margin: 2px;
    border: none;
    &:hover {
      background-color: var(--plava);
    }
    & a {
      display: block;
      color: #fff;
      letter-spacing: 0.09em;
      text-transform: uppercase;
      padding: 0 0 0.8em 0;
      font-size: 1.5rem;
      & span {
        font-size: 0.9rem;
        display: block;
        background-color: var(--tplava);
        padding: 4px 3px 2px;
        margin-bottom: 0.8em;
      }
    }
    /* @media (--tablet) {
      flex: 1 1 20%;
    } */
  }
}

/* NO DATE nema dostupnih datuma za booking */

.promoizlet-nodates {
  padding: 0 10px 15px 2px;
  border-top: 1px dotted var(--svsiva);
  background: url(sorry.svg) 10px 50% no-repeat;
  background-size: 30px 30px;
  & p {
    padding-left: 50px;
    font-size: 0.9em;
    color: var(--siva);
  }
}

/* // PRIKAZ MAXI PROMOIZLETA NA NASLOVNICI  */
.maxipromoizleti-wrapper {
  width: 100%;
  background-color: var(--zuta);
  & > h2,
  & > h3 {
    max-width: var(--maxSirina);
    margin: 0 auto 0 auto;
    padding: 40px 20px 0 20px;
    text-align: center;
    color: var(--tplava);
  }
}
.maxipromoizleti {
  margin: 5rem auto;
  max-width: var(--maxSirina);
  padding: 20px;

  /* text-align: center; */
  display: grid;
  justify-content: center;
  align-items: stretch;
  align-content: start;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
  @media (--tablet) {
    grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  }
  /* grid-template-rows: repeat(3, auto); */
}

.maxipromoizleti {
  & h3 {
    font-size: calc(var(--fontSize) * 1.8);
  }
  & .promoizletblok {
    position: relative;
    overflow: hidden;
    min-height: 280px;
  }
  & .note {
    padding: 12px 80px 80px;
    background-color: var(--crvena);
    color: #fff;
    transform: rotate(-35deg);
    position: absolute;
    bottom: -50px;
    right: -85px;
    font-size: 1.8rem;
    font-weight: bold;
  }
}
