/* // SEARCH FORMA */
.searchbar {
  position: relative;
  background-color: var(--plava);
  overflow: hidden;
}

.searchcall {
  text-align: center;
  margin: 0.5em auto;
  padding: 5px;
  color: #fff;
  transition: all 0.2s linear;
  &:after {
    content: '';
    display: inline-block;
    margin-left: 15px;
    width: 20px;
    height: 20px;
    background: url(povecalo1.svg);
    margin-bottom: -3px;
  }
  &:hover {
    cursor: pointer;
    color: var(--tplava);
  }
}

.searchizlet {
  max-height: 0;

  top: 0;

  transform: scaleY(0.5);
  transform: scaleX(0);
  transition: all 0.2s linear;

  box-sizing: border-box;
  padding: 0em;
  overflow: hidden;

  line-height: 1.2em;

  color: #fff;
}

.visiblesearch {
  padding: 2em 1em;

  transform: scaleY(1);
  transform: scaleX(1);
  max-height: 700px;
  z-index: 999;
  @media (--large) {
    max-height: 200px;
  }
}

.searchform {
  margin: 0;
  padding: 0;

  display: flex;
  align-items: center;

  justify-content: space-around;
  flex-wrap: nowrap;
  flex-direction: column;
  @media (--large) {
    justify-content: center;
    flex-direction: row;
  }

  & select {
    background: transparent;
    border: 1px dotted #fff;
    background-color: #fff;
    background-color: rgb(255 255 255 / 0.7);
    font-size: 1em;
    color: var(--tplava);
    padding: 5px;
    padding-right: 30px;
    -webkit-appearance: none;
    -webkit-border-radius: 0px;
    background-image: url("data:image/svg+xml;utf8,<svg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='24' height='24' viewBox='0 0 24 24'><path fill='#444' d='M7.406 7.828l4.594 4.594 4.594-4.594 1.406 1.406-6 6-6-6z'></path></svg>");
    background-position: 100% 50%;
    background-repeat: no-repeat;
    /* If you add too much padding here, the options won't show in IE */
  }
  & input {
    background: transparent;
    border: 1px dotted #fff;
    background-color: #fff;
    background-color: rgb(255 255 255 / 0.7);
    font-size: 1em;
    color: rgb(var(--c-tplava));
    padding: 5px;
    /* If you add too much padding here, the options won't show in IE */
  }
  & input[type='checkbox'] {
    -webkit-appearance: none;
    background-color: #fff;
    background-color: rgb(255 255 255 / 0.7);
    border: 1px dotted #fff;
    box-shadow: none;
    margin: 2px;
    padding: 12px;
    border-radius: 2px;
    display: inline-block;
    position: relative;
    &:active,
    &:checked:active {
      box-shadow: none;
    }
    &:checked {
      background-color: var(--svzelena);
      box-shadow: none;
      color: #fff;
      padding: 14px;
      margin: 0;
    }
    &:checked:after {
      position: absolute;
      display: block;
      top: -1px;
      left: -1px;
      content: '';
      width: 32px;
      height: 32px;
      background: url(check.svg);
    }
  }
}

.searchform li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  list-style-image: none;
  width: 100%;
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  margin-bottom: 10px;
  @media (--large) {
    width: auto;
    align-self: stretch;
    flex-direction: column;
    margin: 0 10px;
    flex: 0 1 auto;
  }
}

.searchform .libuton {
  align-self: flex-end;
}

.searchform li label,
.searchform li p {
  width: 50%;
  align-self: center;
  margin-bottom: 0px;
  margin-right: 10px;
  @media (--large) {
    align-self: flex-start;
    width: auto;
    margin-bottom: 10px;
  }
  flex: 0 0 auto;
  text-align: right;
  font-weight: normal;
  color: #fff;
  font-size: 0.9em;
  text-transform: uppercase;
}

.searchform input,
.searchform button,
.searchform select {
  order: 2;
  flex: 0 0 auto;
  text-align: left;
  align-self: center;

  @media (--large) {
    align-self: flex-start;
  }
}

.searchform li.ds {
  width: auto;
  align-self: stretch;
  flex-direction: column;
  flex: 0 1 auto;
  margin: 0 10px 30px 10px;
  @media (--large) {
    margin: 0 10px;
  }
  & label {
    align-self: center;
    @media (--large) {
      align-self: flex-start;
    }
    width: auto;
    margin-bottom: 10px;
  }
  & select {
    align-self: center;
    @media (--large) {
      align-self: flex-start;
    }
  }
}

