#searchbuton {
  padding: 7px 12px;
  border: none;
  background: var(--zelena);
  border-bottom: 3px solid rgb(255 255 255 / 0.7);
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: 2px;
  &:hover {
    cursor: pointer;
    background: var(--svzelena);
    border-bottom: 3px solid #fff;
  }
}
