/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/

.menubuton {
  text-indent: -9999px;
  overflow: hidden;
  border: 4px solid var(--zuta);
  font-size: 1.2em;
  z-index: 9999;
  position: fixed;
  top: 14px;
  right: 15px;
  background: var(--zuta) url(but-menu.svg);
  width: 24px;
  height: 24px;
  margin: 0;
  transition: font-size 0.3s linear;

  &:hover {
    background-color: #fff;
    border-color: #fff;
    cursor: pointer;
  }
}

.menuout .menubuton {
  background: var(--zuta) url(but-menu-close.svg) no-repeat 50% 50%;
  background-size: cover;
  border: 4px solid var(--zuta);
  /* @include sprite(but-menu-close, bg); */
  &:hover {
    background-color: #fff;
    border-color: #fff;
    cursor: pointer;
    font-size: 1.3em;
  }
}

.content {
  position: relative;
}

.menuout .content {
  overflow: hidden;
}

/* // Kompletni blok izbornika */
.mainmenu {
  max-width: var(--maxSirina);
  margin: auto;
  padding: 20px;
  color: var(--svplava);

  & a,
  & a:visited,
  & a:link {
    color: #fff;

    &:hover {
      color: var(--plava);
    }
  }

  & h3,
  & p {
    color: var(--svplava);
  }

  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: center;
  flex-wrap: nowrap;
  text-align: center;
  transition: all 0.2s linear;
}

.mainmenuoverlay {
  /* Height & width depends on how you want to reveal the overlay (see JS below) */
  height: 100%;
  width: 0;
  position: fixed;
  /* Stay in place */
  z-index: 99;
  /* Sit on top */
  right: 0;
  top: 0;
  /* background-color: var(--tplava); */
  /* background-color: rgba($tplava, 0.94); */
  background-color: var(--tplava);
  overflow-x: hidden;
  overflow-y: hidden;
  /* Disable horizontal scroll */
  transition: 0.4s;
  /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
}

.visible {
  width: 100vw;
  overflow-y: auto;
  @media (--large) {
    width: 75vw;
  }
}

/* // Zaglavlje izbornika (logo) */
.menutitle {
  margin: 0 auto 1.6rem;
  /* font-size: 0.5em; */

  /* @media (--tablet) {
    font-size: 0.7em;
  } */

  text-indent: -9999px;
  overflow: hidden;
  /* @include sprite(croatiaexcursion-logo, all); */
  width: 292px;
  height: 34px;
  background: url(croatiaexcursion-logo.svg) 50% 50% no-repeat;
  background-size: cover;
}

/* // Izbornik jezika */
.menujezik {
  & ul,
  & li {
    list-style-type: none !important;
    list-style-image: none !important;
    margin: 0;
    padding: 0;
  }

  & ul {
    padding: 20px;
    text-align: center;
    display: flex;
    justify-content: center;
  }

  & a {
    box-sizing: border-box;
    display: block;
  }
}

.mainmenu .menujezik {
  & a {
    padding: 0.6em 0.8em;

    &.active {
      border-bottom: 1px dotted var(--zuta);
    }

    &:hover,
    &:hover.active {
      background-color: #fff;
    }
  }

  @media (--large) {
    display: none;
  }
}

.partnerlogin {
  align-self: center;
  margin: 1.5em;
  padding: 0.5em 1em;
  border: 1px dotted #fff;
}

/* // Izdvojeni izlet u izborniku */
.menuizlet {
  position: relative;
  /* // omjer izleta u izborniku 8:6 */
  width: 90vw;
  height: 60vw;
  max-height: 400px;
  max-width: 600px;
  margin: 1em auto;

  & .izletipasica h2 {
    text-align: center;
    font-size: 1.2em;
    font-weight: normal;
  }
}

/* // izbornik - popis svih izleta */
.menuizleti {
  display: none;

  @media (--large) {
    & a {
      display: block;

      font-size: 1em;
      padding: 0.5em;
      line-height: 1.2em;
      font-weight: 400;
      transform: all 0.2s linear;
    }

    padding: 0.2em 0;
    margin: 0 auto;
    max-width: 1000px;
    display: flex;
    flex-flow: row wrap;
  }
}

.submenuizleti {
  background-color: var(--plava);
  box-sizing: content-box;
  margin: 2px;
  flex: 1 1 100%;

  & a:last-child {
    margin-bottom: 10px;
  }

  & a,
  & a:visited,
  & a:link {
    &:hover {
      color: var(--tplava);
    }
  }

  @media (--tablet) {
    /* // justify-content: center; */
    flex: 1 1 30%;
  }
}

.menuglavnestavke {
  & a {
    display: block;
    font-size: 1.2em;
    padding: 0.5em 0;
    font-weight: normal;
    transform: all 0.2s linear;
  }

  padding: 0.5em 1.5em;
  margin: 0 auto;
  border-bottom: 1px dotted var(--tplava);

  @media (--large) {
    border: none;
  }
}

.menustavke {
  & a {
    display: block;
    padding: 0.5em 0;
    font-weight: normal;
    transform: all 0.2s linear;
  }

  padding: 0.2em 1.5em;
  margin: 0 auto 1.5em;
}

/* // linkovi na drustvene mreze u izborniku (ikone definirane u _ikone.scss) */

.mainmenu .menusocialnetwork {
  & a,
  & a:visited {
    background-color: #fff;
    border: 4px solid #fff;

    &:hover {
      background-color: var(--plava);
      border: 4px solid var(--plava);
    }
  }
}

.menusocialnetwork {
  & a,
  & a:visited {
    text-indent: -9999px;
    /* font-size: 1.4em; */
    margin: 2px;
    border: 4px solid var(--zuta);
    display: inline-block;
    overflow: hidden;
    width: 32px;
    height: 32px;
    background-size: cover;
    &:hover {
      background-color: #fff;
      border: 4px solid #fff;
    }
  }
}

.sn-fb {
  background: url(sn-facebook.svg) 50% 50% no-repeat;
}

.sn-yb {
  background: url(sn-youtube.svg) 50% 50% no-repeat;
}

.sn-in {
